type Verdict =
  | "genius"
  | "nice-demo"
  | "over-engineered"
  | "elegant"
  // NEW for Serious Science
  | "calm-down"
  | "caution"
  | "red-flag"
  | "overhyped";

const STYLE: Record<Verdict, string> = {
  genius: "bg-pink-50 text-pink-700",
  "nice-demo": "bg-blue-50 text-blue-700",
  "over-engineered": "bg-red-50 text-red-700",
  elegant: "bg-green-50 text-green-700",
  // NEW
  "calm-down": "bg-slate-50 text-slate-700",
  caution: "bg-amber-50 text-amber-800",
  "red-flag": "bg-rose-50 text-rose-700",
  overhyped: "bg-purple-50 text-purple-700",
};

export default function VerdictBadge({ verdict }: { verdict: Verdict }) {
  return (
    <span className={`inline-flex items-center rounded-full px-2 py-1 text-xs ${STYLE[verdict]}`}>
      {labelize(verdict)}
    </span>
  );
}

function labelize(v: Verdict) {
  return (
    {
      genius: "Genius curiosity",
      "nice-demo": "Nice demo",
      "over-engineered": "Over-engineered",
      elegant: "Elegant nonsense",
      "calm-down": "Calm down",
      caution: "Caution",
      "red-flag": "Red flag",
      overhyped: "Overhyped",
    }[v] ?? v
  );
}
